.page-header {
  width: 100%;
  max-width: $max-width;
  padding: 0px 40px;
  margin: 20px auto;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    max-width: $max-tablet-width;
    flex-direction: column;
    &--menu-opened {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      z-index: 23;
      left: 0;
      background: $color-bg-body;
    }
  }

  @media (max-width: $mobile-width) {
    max-width: $max-mobile-width;
    min-width: $max-mobile-width;
    padding: 0px 16px;
  }
}

.page-header__navigation {
  flex-grow: 1;
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  @media (max-width: $tablet-width) {
    
    width: 100%;
    display: flex;
    justify-content: space-between;

    
  }
}

.page-header__logo {
  line-height: 0px;
}

.page-header__open-menu {
  display: none;

  @media (max-width: $tablet-width) {
    position: relative;
    display: block;
    width: 44px;
    height: 44px;
    top: 8px;
    right: 0;
    padding: 0; 
  }
}